@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --label-label-large: Roboto;

  /* font sizes */
  --label-label-large-size: 16px;
  --font-size-smi: 13px;
  --headline-headline-large-size: 32px;

  /* Colors */
  --primary-shades-primary-lighter: #e4ecfc;
  --base-colors-primary: #2360d8;
  --base-colors-white: #fff;
  --gray-shades-lightest-gray: #e9eaec;
  --gray-shades-gray: #9298a3;
  --gray-shades-dark-gray: #636973;
  --base-colors-black: #080a30;

  /* Gaps */
  --gap-21xl: 40px;
  --gap-base: 16px;

  /* Paddings */
  --padding-3xs: 10px;

  /* border radiuses */
  --br-9xs: 4px;
  --br-8xs: 5px;
}
