.buttonParent {
  height: 116px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.buttonParent,
.form,
.frameGroup {
  align-self: stretch;
  display: flex;
  flex-direction: column;
}
.form {
  height: 225px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 29px;
}
.frameGroup {
  align-items: center;
  justify-content: center;
  gap: var(--gap-21xl);
}
.polticasDePrivacidad {
  flex: 1;
  position: relative;
  line-height: 140%;
}
.frameParent,
.legales,
.signUp {
  display: flex;
  justify-content: center;
}
.legales {
  width: 358px;
  height: 18px;
  flex-direction: row;
  align-items: flex-end;
  gap: 50px;
}
.frameParent,
.signUp {
  flex-direction: column;
  align-items: center;
}
.frameParent {
  align-self: stretch;
  flex: 1;
  padding: 60px 0 0;
  gap: 104px;
}
.signUp {
  position: relative;
  background-color: #f4f5f9;
  width: 100%;
  height: 844px;
  overflow: hidden;
  padding: 16px 20px;
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-smi);
  color: var(--base-colors-primary);
  font-family: var(--label-label-large);
}
