.textHere {
  flex: 1;
  position: relative;
  line-height: 140%;
}
.label {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.property1default {
  position: relative;
  border-radius: var(--br-8xs);
  background-color: var(--base-colors-white);
  border: 1px solid var(--gray-shades-lightest-gray);
  box-sizing: border-box;
  width: 350px;
  height: 60px;
  display: flex;
  flex-direction: column;
  padding: var(--padding-3xs);
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  font-size: var(--label-label-large-size);
  color: var(--gray-shades-gray);
  font-family: var(--label-label-large);
}
